#we-fullpage .fp-controlArrow {
  position: absolute;
  z-index: 4;
  cursor: pointer;
  width: 72px!important;
  height: 72px!important;
  border-style: solid;
  margin-top: -38px;
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  background-color: #ffffff;
}
#we-fullpage .fp-controlArrow.fp-prev {
  border: none;
  transition: all .5s ease;
}
#we-fullpage .fp-controlArrow.fp-prev:hover {
  background-color: #f52d42;
}
#we-fullpage .fp-controlArrow.fp-prev:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 5%;
  left: 0;
  z-index: 5;
  background-image: url(../img/images/team-slider-arrow.png);
  background-repeat: no-repeat;
  background-position: 28px 50%;
  border: none;
}
#we-fullpage .fp-controlArrow.fp-prev:hover:before {
  background-image: url(../img/images/team-slider-arrow--hover.png);
  transform: rotate(180deg);
  left: -3%;
}
#we-fullpage .fp-controlArrow.fp-next {
  border: none;
  transition: all .5s ease;
}
#we-fullpage .fp-controlArrow.fp-next:hover {
  background-color: #f52d42;
}
#we-fullpage .fp-controlArrow.fp-next:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 5%;
  right: -3%;
  z-index: 5;
  background-image: url(../img/images/team-slider-arrow.png);
  transform: rotate(180deg);
  background-repeat: no-repeat;
  background-position: 28px 50%;
  border: none;
}
#we-fullpage .fp-controlArrow.fp-next:hover:before {
  background-image: url(../img/images/team-slider-arrow--hover.png);
  transform: rotate(360deg);
}
